import React, { useEffect, useState } from 'react';
import Header from '../components/Header';

const Cart = () => {
    const [cartItems, setCartItems] = useState([]);
    const [totalPrice, setTotalPrice] = useState(0);
    const [loading, setLoading] = useState(true);
    const apiBaseUrl = process.env.REACT_APP_API_BASE_URL;

    useEffect(() => {
        fetch(`${apiBaseUrl}/cart`, {
            credentials: "include",
        })
            .then(res => res.json())
            .then(data => {
                setCartItems(data.cartItems);
                setTotalPrice(data.totalPrice);
                setLoading(false);
            })
            .catch(err => {
                console.error("", err);
                setLoading(false);
            });
    }, []);

    // 
    const fetchCart = () => {
        fetch(`${apiBaseUrl}/cart`, {
            credentials: "include",
        })
            .then(res => res.json())
            .then(data => {
                setCartItems(data.cartItems);
                setTotalPrice(data.totalPrice);
            })
            .catch(err => {
                console.error("", err);
            });
    };

    //
    const handleQuantityChange = (id, newQuantity) => {
        setCartItems(prev =>
            prev.map(item =>
                item.id === id ? { ...item, quantity: newQuantity } : item
            )
        );
    };


    //
    const handleUpdate = async () => {
        const params = new URLSearchParams();
        cartItems.forEach(item => {
            params.append("cartItemIds", item.id);
            params.append("quantities", item.quantity);
        });
        try {
            const response = await fetch(`${apiBaseUrl}/cart/update`, {
                method: "POST",
                headers: {
                    "Content-Type": "application/x-www-form-urlencoded",
                },
                credentials: "include",
                body: params,
            });

            if (response.ok) {
                alert("");
                fetchCart(); // 
            } else {
                alert("");
            }
        } catch (err) {
            console.error(":", err);
        }
    }

    //
    const handleDelete = async (cartItemId) => {
        const confirmDelete = window.confirm("");
        if (!confirmDelete) return;

        try {
            const response = await fetch(`${apiBaseUrl}/cart/delete`, {
                method: "POST",
                headers: {
                    "Content-Type": "application/x-www-form-urlencoded",
                },
                credentials: "include",
                body: new URLSearchParams({
                    cartItemId: cartItemId,
                }),
            });

            if (response.ok) {
                setCartItems(prev => prev.filter(item => item.id !== cartItemId));
                fetchCart(); // 
            } else {
                alert("");
            }
        } catch (err) {
            console.error(":", err);
        }
    }

    //
    const handleCheckout = async () => {
        if (cartItems.length === 0) {
            alert("");
            return;
        }

        const confirmCheckout = window.confirm("");
        if (!confirmCheckout) return;

        try {
            const response = await fetch(`${apiBaseUrl}/order/checkout`, {
                method: "POST",
                credentials: "include",
            });

            const data = await response.json();

            if (response.ok) {
                alert(data.message); // 
                // React Router
                window.location.href = "/OrderComplete";
            } else {
                alert(data.error || "");
            }
        } catch (error) {
            console.error(":", error);
            alert("");
        }
    };


    if (loading) return <p>...</p>;

    return (
        <>            {/** */}
            <Header />
            <div>
                <h1></h1>
                <table border="1">
                    <thead>
                        <tr>
                            <th></th>
                            <th></th>
                            <th></th>
                            <th></th>
                        </tr>
                    </thead>
                    <tbody>
                        {cartItems.map(item => {
                            const price = item.product.salePrice || item.product.price;
                            const subtotal = price * item.quantity;
                            return (
                                <tr key={item.id}>
                                    <td>{item.product.name}</td>
                                    <td>{price}</td>
                                    <td><input
                                        type="number"
                                        min="1"
                                        value={item.quantity}
                                        onChange={(e) =>
                                            handleQuantityChange(item.id, parseInt(e.target.value, 10))
                                        }
                                    />
                                    </td>
                                    <td>{subtotal}</td>
                                    <td><button onClick={() => handleDelete(item.id)}></button></td>
                                    <td><button onClick={handleUpdate} style={{ padding: '10px 20px' }}></button></td>
                                </tr>
                            );
                        })}
                    </tbody>
                </table>
                <p><strong>:</strong> {totalPrice}</p>
                <button onClick={handleCheckout} style={{ padding: '10px 20px', marginTop: '20px' }}>
                    
                </button>
            </div>
        </>
    );
};

export default Cart;