
import React, { useEffect, useState } from "react";
import Header from '../components/AdminHeader';

function AdminHome() {
    const [products, setProducts] = useState([]);
    const apiBaseUrl = process.env.REACT_APP_API_BASE_URL;
    const [newProduct, setNewProduct] = useState({
        name: "",
        price: "",
        stock: "",
        description: "",
        imagePath: "",
    });
    const [message, setMessage] = useState("");
    const [loading, setLoading] = useState(true);

    // //
    useEffect(() => {
        fetch(`${apiBaseUrl}/admin/products`, {
            method: "GET",
            credentials: "include", // Cookie
        })
            .then(res => res.json())
            .then(data => {
                setProducts(data);
                setLoading(false);
                console.log(data);
            })
            .catch(err => {
                console.error("", err);
                setLoading(false);
            });

    }, []);

    const fetchProducts = async () => {
        try {
            const res = await fetch(`${apiBaseUrl}/admin/products`, {
                method: "GET",
                credentials: "include", //  
            });
            const data = await res.json();
            setProducts(data);
        } catch (err) {
            console.error("", err);
        }
    };

    const handleDelete = async (id) => {
        try {
            await fetch(`${apiBaseUrl}/admin/products/delete/${id}`,
                {
                    method: "DELETE",
                    credentials: "include",
                });
            setMessage("");
            fetchProducts();
        } catch (err) {
            console.error("", err);
        }
    };


    const handleAdd = async (e) => {
        e.preventDefault();
        try {
            await fetch(`${apiBaseUrl}/admin/products/add`,
                {
                    method: "POST",
                    credentials: "include",
                    headers: {
                        "Content-Type": "application/json",
                    },
                    body: JSON.stringify({
                        ...newProduct,//Product
                        price: Number(newProduct.price),
                        stock: Number(newProduct.stock),
                    }),

                });
            setMessage("");
            setNewProduct({ name: "", price: "", stock: "", description: "", imagePath: "" });
            fetchProducts();
        } catch (err) {
            console.error("", err);
        }
    };

    const handleUpdate = async (product) => {
        try {
            await fetch(`${apiBaseUrl}/admin/products/update`,
                {
                    method: "PUT",
                    credentials: "include",
                    headers: {
                        "Content-Type": "application/json",
                    },
                    body: JSON.stringify({
                        ...product,
                        price: Number(product.price),
                        stock: Number(product.stock),
                    }),
                });
            setMessage("");
            fetchProducts();
        } catch (err) {
            console.error("", err);
        }
    };


    return (
        <div className="p-6">
            <>            {/** */}
                <Header />

                <h2 className="text-xl font-bold mb-4"></h2>
                {message && <p className="text-green-600">{message}</p>}

                <form onSubmit={handleAdd} className="mb-6 space-y-2">
                    <h3 className="text-lg font-semibold"></h3>
                    <input
                        type="text"
                        placeholder=""
                        value={newProduct.name}
                        onChange={(e) => setNewProduct({ ...newProduct, name: e.target.value })}
                        required
                    />
                    <input
                        type="number"
                        placeholder=""
                        value={newProduct.price}
                        onChange={(e) => setNewProduct({ ...newProduct, price: e.target.value })}
                        required
                    />
                    <input
                        type="number"
                        placeholder=""
                        value={newProduct.stock}
                        onChange={(e) => setNewProduct({ ...newProduct, stock: e.target.value })}
                        required
                    />
                    <input
                        type="text"
                        placeholder=""
                        value={newProduct.description}
                        onChange={(e) => setNewProduct({ ...newProduct, description: e.target.value })}
                    />
                    <input
                        type="text"
                        placeholder="URL"
                        value={newProduct.imagePath}
                        onChange={(e) => setNewProduct({ ...newProduct, imagePath: e.target.value })}
                    />
                    <button type="submit" className="bg-blue-500 text-white px-4 py-1 rounded"></button>
                </form>

                <h3 className="text-lg font-semibold mb-2"></h3>
                <table className="w-full table-auto border border-collapse border-gray-300">
                    <thead>
                        <tr className="bg-gray-100">
                            <th></th>
                            <th></th>
                            <th></th>
                            <th></th>
                            <th></th>
                        </tr>
                    </thead>
                    <tbody>
                        {products.map((prod) => (
                            <tr key={prod.id} className="border-b">
                                <td>
                                    <input
                                        type="text"
                                        value={prod.name}
                                        onChange={(e) =>
                                            setProducts(products.map((p) =>
                                                p.id === prod.id ? { ...p, name: e.target.value } : p
                                            ))
                                        }
                                    />
                                </td>
                                <td>
                                    <input
                                        type="number"
                                        value={prod.price}
                                        onChange={(e) =>
                                            setProducts(products.map((p) =>
                                                p.id === prod.id ? { ...p, price: e.target.value } : p
                                            ))
                                        }
                                    />
                                </td>
                                <td>
                                    <input
                                        type="number"
                                        value={prod.stock}
                                        onChange={(e) =>
                                            setProducts(products.map((p) =>
                                                p.id === prod.id ? { ...p, stock: e.target.value } : p
                                            ))
                                        }
                                    />
                                </td>
                                <td>
                                    <input
                                        type="text"
                                        value={prod.imageUrl}
                                        onChange={(e) =>
                                            setProducts(products.map((p) =>
                                                p.id === prod.id ? { ...p, imageUrl: e.target.value } : p
                                            ))
                                        }
                                    />
                                </td>
                                <td>
                                    <button onClick={() => handleUpdate(prod)} className="text-blue-600 mr-2">
                                        
                                    </button>
                                    <button onClick={() => handleDelete(prod.id)} className="text-red-600">
                                        
                                    </button>
                                </td>
                            </tr>
                        ))}
                    </tbody>
                </table>
            </>
        </div>

    );
}


export default AdminHome;
