import React from "react";

import { productItems } from "assets/data/productItems";//20250905
import { PageTitle } from "components/atoms/PageTitle";//20250905
import { CardList } from "components/organisms/CardList";//20250905
import Header from "../components/PortfolioHeader.jsx";

const MyProduct = () => {
  return (
    <>
    <Header />
      <PageTitle>PRODUCT</PageTitle>
      <CardList
        cardList={productItems}
        isSkillLevel={false}
        isUsedTech={true}
        isIcon={true}
        flexwrap="wrap-reverse"
        titleTechImg=""
      />
    </>
  );
};
export default MyProduct;
